h1,
h2,
h3,
h4,
h5 {
  margin: 0;
}

.font-h1-didona {
  font-family: 'Didona', sans-serif;
  font-size: 1.75rem;
  font-weight: 400;
  line-height: 2.25rem;
  text-transform: uppercase;
}

.font-h2-didona {
  font-family: 'Didona', sans-serif;
  font-size: 2.1875rem;
  font-weight: 400;
  line-height: 2.0625rem;
  text-transform: uppercase;
  letter-spacing: 0.03em;
}

.font-h3-didona {
  font-family: 'Didona', sans-serif;
  font-size: 1.625rem;
  font-weight: 400;
  line-height: 2.125rem;
  text-transform: uppercase;
  letter-spacing: 0.02em;
}

.font-h1 {
  font-family: "Roboto Light", sans-serif;
  font-size: 1.75rem;
  font-weight: 300;
  line-height: 2.125rem;
}

.font-h2 {
  font-size: 0.9375rem;
  font-weight: 400;
  line-height: 1.625rem;
  letter-spacing: 0.01em;
}

.font-h2-medium {
  font-family: "Roboto Medium", sans-serif;
  font-size: 1.375rem;
  font-weight: 500;
  line-height: 1.875rem;
}

.font-h2-light {
  font-family: "Roboto Light", sans-serif;
  font-size: 0.9375rem;
  font-weight: 300;
  line-height: 1.625rem;
  letter-spacing: 0.01em;
}

.font-text-m {
  font-family: "Roboto Light", sans-serif;
  font-size: 1.625rem;
  font-weight: 300;
  line-height: 2.1875rem;
}

.font-text-s {
  font-family: "Roboto Light", sans-serif;
  font-size: 0.9375rem;
  font-weight: 300;
  line-height: 1.3125rem;
}

.font-button {
  font-family: "Roboto Light", sans-serif;
  font-size: 0.9375rem;
  font-weight: 300;
  line-height: 1.625rem;
}

.font-menu {
  font-family: "Roboto Light", sans-serif;
  font-size: 1rem;
  font-weight: 300;
  line-height: 1.25rem;
}

.s-main-video .l-container {
  max-width: 100%;
  padding-right: 0;
  padding-left: 0;
}

.s-main-video__text {
  max-width: 47.5rem;
}

.s-main-video__media {
  position: relative;
}

.s-main-video__cover {
  width: 100%;
  height: auto;
  max-width: 90rem;
  max-height: 37.5rem;
  margin: 0 auto;
  object-fit: cover;
}

.s-main-video__btn {
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 2.625rem;
  height: 3.375rem;
  border: 0;
  transform: translate(-50%, -50%);
  transition: 0.5s all ease;
  background-color: transparent;
}

.s-main-video__btn:hover {
  opacity: 0.7;
}

.s-blog-articles .swiper-controls {
  justify-content: flex-end;
}

.s-blog-articles__card-image {
  margin-bottom: 1rem;
}

.s-blog-articles__card-image img {
  width: 100%;
  height: auto;
}

.s-blog-articles__card-title {
  max-width: 26.875rem;
  margin-bottom: 0.625rem;
  text-transform: uppercase;
}

.s-blog-articles__card-text {
  max-width: 30rem;
}

@media (min-width: 768px) {
  .s-main-video__info {
    max-width: 94%;
    margin: 0 auto 3.125rem;
  }
}

@media screen and (min-width: 1200px) {
  .font-h1-didona {
    font-size: 3.125rem;
    font-weight: 400;
    line-height: 3.75rem;
    letter-spacing: 0.01em;
  }

  .font-h1 {
    font-size: 3.125rem;
    line-height: 4.0625rem;
  }

  .font-h2 {
    font-size: 1.125rem;
    line-height: 1.625rem;
  }

  .font-h2-light {
    font-size: 1.125rem;
    line-height: 1.625rem;
  }

  .font-text-s {
    font-size: 1.125rem;
    line-height: 1.5625rem;
  }

  .font-button {
    font-size: 1.125rem;
    line-height: 1.625rem;
  }
}

@media screen {
  .px-0 {
    padding-right: 0;
    padding-left: 0;
  }

  .py-0 {
    padding-top: 0;
    padding-bottom: 0;
  }

  .pb-0 {
    padding-bottom: 0;
  }

  .pt-0 {
    padding-top: 0;
  }

  .mx-0 {
    margin-right: 0;
    margin-left: 0;
  }

  .my-0 {
    margin-top: 0;
    margin-bottom: 0;
  }

  .mb-0 {
    margin-bottom: 0;
  }

  .mt-0 {
    margin-top: 0;
  }

  .br-0 {
    border-radius: 0;
  }
}